* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box
}

body {
	width: 100%;
	max-width: 1000px;
	margin: 0 auto;
	padding: 5px 0
}

#recoeve-container {
	font-size: 10px;
	line-height: 1.5;
	padding: 0 .7em;
	font-family: 'Malgun Gothic', '맑은 고딕', 나눔고딕, NanumGothic, 'MS Mincho', Tahoma, Sans-serif;
	background: rgb(40, 40, 55);
	color: white
}

h1 {
	display: block;
	font: inherit;
	font-size: 3em;
	font-weight: bold;
	margin: 0 0 .5em;
	padding: .6em .5em;
	border-bottom: 5px solid rgb(140, 140, 150)
}

.form-login {
	width: 20em;
	max-width: 100%;
	margin: 0 auto 1em;
	padding: 1em;
	font-size: 16px;
	color: white;
	line-height: 1;
	background: black;
	border: 3px solid rgb(120, 120, 130)
}

.form-login>h2 {
	margin: 0 0 1em;
	font: inherit;
	font-size: 1.3em;
	line-height: 1.3;
	font-weight: bold
}

.form-login>label {
	display: block;
	color: white;
	font-size: 1em;
	font-weight: bold;
	margin: .9em 0 .4em
}

.form-login>label:first-of-type {
	margin-top: 0
}

.form-login>label.checkbox {
	font-weight: normal
}

.form-login>input {
	display: block;
	width: 100%;
	height: auto;
	overflow: hidden;
	padding: .3em .5em;
	font-family: inherit;
	font-weight: normal;
	font-size: 1em;
	line-height: 1.5;
	background: white;
	color: rgb(50, 50, 50)
}

.form-login>input:focus {
	background: rgb(240, 240, 255)
}

.form-login>button {
	width: 100%;
	font: inherit;
	font-weight: bold;
	padding: .3em .3em .5em;
	text-align: center;
	margin-top: 1.3em
}

.form-login>button:disabled {
	background: rgb(70, 70, 70)
}

div.form-login>button {
	margin-top: 0
}

.form-login>.error-msg {
	border: 1px solid gray;
	padding: .3em .5em;
	margin: .5em 0 0;
	color: rgb(255, 200, 200);
	line-height: 1.3
}

.logo-google {
  width: 21px;
  display: inline-block;
  margin-right: 1em;
  vertical-align: sub
}

.g_id_signin>div>iframe {
	margin: -2px auto !important
}

.desc {
	padding: .5em
}

data {
	display: none
}

.lang {
	padding: .5em
}

.p {
	font-size: 1.6em;
	padding-bottom: 1em
}

.rC>.rSC {
	position: relative;
	height: 3em;
	padding-bottom: 63%
}

.rC>.rSC>* {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0
}

.bold {
	font-weight: bold
}

.reco {
	color: rgb(200, 130, 130)
}

.eve {
	color: rgb(130, 130, 200)
}

a {
	text-decoration: none
}

a:link {
	color: rgb(190, 190, 235)
}

a:visited {
	color: rgb(170, 170, 215)
}

a:active {
	color: rgb(100, 215, 215)
}

a:hover {
	color: rgb(170, 215, 170);
	text-decoration: underline
}

#foot {
	clear: both;
	border-top: 2px solid white;
	padding: 1em .5em;
	font-size: 1.5em
}

@media all and (min-width:640px) {
	.form-login {
		margin: 0 .6em 1em 1em;
		float: right;
		clear: right
	}
}
